import React, { useState } from "react";
import logo from "../../logo.svg";
import style from "./styles/Header.module.css";
import { useNavigate } from "react-router-dom";
import { slide as Menu } from "react-burger-menu";
import { Link } from "react-router-dom";
import navStyle from "./../Aside/styles/Aside.module.css";

const Header = () => {
  const navigate = useNavigate();

  const navigateToStart = () => {
    navigate("/Start");
  };

  let burgerStyles = {
    bmBurgerButton: {
      position: "fixed",
      width: "36px",
      height: "30px",
      left: "10px",
      top: "15px",
    },
    bmBurgerBars: {
      background: "rgb(97, 218, 251)",
      height: "10%",
    },
    bmBurgerBarsHover: {
      background: "#a90000",
    },
    bmCrossButton: {
      height: "24px",
      width: "24px",
    },
    bmCross: {
      background: "#bdc3c7",
    },
    bmMenuWrap: {
      position: "fixed",
      height: "100%",
    },
    bmMenu: {
      background: "#373a47",
      padding: "2.5em 1.5em 0",
      fontSize: "1.15em",
    },
    bmMorphShape: {
      fill: "#373a47",
    },
    bmItemList: {
      color: "#b8b7ad",
      padding: "0.8em",
    },
    bmItem: {
      display: "inline-block",
    },
    bmOverlay: {
      background: "rgba(0, 0, 0, 0.3)",
    },
  };

  if (window.screen.width > 1000) {
    burgerStyles.bmBurgerButton.display = "none";
  }

  const [isOpen, setOpen] = useState(false);

  const handleIsOpen = () => {
    setOpen(!isOpen);
  };

  const closeSideBar = () => {
    setOpen(false);
  };

  return (
    <header className={style.Header}>
      <div className="container-fluid p-0 m-0 d-flex">
        <Menu
          styles={burgerStyles}
          isOpen={isOpen}
          onOpen={handleIsOpen}
          onClose={handleIsOpen}
        >
          <ul className={`${navStyle.ul}`} closeSideBar={closeSideBar}>
            <li className={`${navStyle.li}`} onClick={closeSideBar}>
              <Link to="/Profile">Профиль</Link>
            </li>
            <li className={`${navStyle.li}`} onClick={closeSideBar}>
              <Link to="/Dialogs">Сообщения</Link>
            </li>
            <li className={`${navStyle.li}`} onClick={closeSideBar}>
              <Link to="/News">Новости</Link>
            </li>
            <li className={`${navStyle.li}`} onClick={closeSideBar}>
              <Link to="/Music">Музыка</Link>
            </li>
            <li className={`${navStyle.li}`} onClick={closeSideBar}>
              <Link to="/Settings">Настройки</Link>
            </li>
          </ul>
        </Menu>
        <div
          className="p-0 m-0 d-flex justify-content-center align-items-center"
          onClick={navigateToStart}
        >
          <img src={logo} alt="Logo" className={style.Header__logo} />
          <span> Kenshi </span>
        </div>
      </div>
    </header>
  );
};

export default Header;
